import styled from "styled-components";
import { Box, Image, Text, ResponsiveContext, Form, Heading, Table, TableHeader, TableRow, TableCell, TableBody, Select } from "grommet";
import React, { useContext, useState, useEffect } from "react";
import Hero from "../../../Components/Hero/Hero";
import BackOfficeDashboardImage from "../../../assets/images/dark-dashboard.jpg";
import RangeCalendar from "../../../Components/_Grommet/Selects/RangeCalendar";
import PatientInput from "../../../Components/_Grommet/Inputs/PatientInput";
import SlimSpinner from "../../../Components/_Grommet/Spinners/SlimSpinner/SlimSpinner";
import {DateRange, IPaymentChannel, IUserBank, FullTransaction} from "../../../Types";
import Axios, { Canceler } from "axios";
import EmptyImage from "../../../assets/illustrations/eastwood-no-messages.png";
import BankList from "../../../_data/banks.json";
import SnackBarComponent, { SnackBarProps } from "../../../Components/SnackBar/SnackBar";
import AssertivePopup, { AssertivePopupProps } from "../../../Components/_Grommet/Popups/AssertivePopup";
import Popup from "reactjs-popup";
import moment from "moment";

const Wrapper = styled(Box)`
	width: 100vw;
	align-items: center;
    padding-bottom: 2rem;
    min-height: 720px;
    
	@media (min-width: 768px) {
		// align-items: start;
	}
`;

interface ITransactionRequest {
    id: string;
    userId: string;
    username: string;
    created: Date;
    transactionDate: Date;
    amount: Number;
    status: string;
    paymentChannel: IPaymentChannel;
    platformBankAccount: IUserBank;
    userBankAccount?: IUserBank;
    description?: string;
}

const Transactions = () => {
    const size = useContext(ResponsiveContext);
    const [loading, setLoading] = useState(false);
    const [snackbar, setSnackbar] = useState({} as SnackBarProps);
    const [popupState, setPopupState] = useState({} as AssertivePopupProps);

    const [dateRange, setDateRange] = useState({start: new Date(), end: new Date()} as DateRange);
	const [searchQuery, setSearchQuery] = useState("");

    const [filter, setFilter] = useState({name: "Deposits", path: "/api/backOffice/Transactions", filter: "type==deposit"});

    const [transaction, setTransaction] = useState([] as FullTransaction[] | ITransactionRequest[]);
    const [isRequest, setIsRequest] = useState(false);

	let cancelRequest: Canceler;
	let _loading: boolean;
    let _refreshRequired: boolean;

    const filters = [
        { name: "Deposits", path: "/api/backOffice/Transactions", filter: "type==Deposit" },
        { name: "Payment Claims", path: "/api/backOffice/Transactions/requests", filter: ""},
        { name: "Pending Claims", path: "/api/backOffice/Transactions/requests", filter: "status==Pending"},
    ];
    
    useEffect(() => {
        getTransactions();
    }, [searchQuery, dateRange, filter]);

    const updateTransaction = (id: string, status: "Approved" | "Declined") => {
        setLoading(true);
        Axios.post("/api/backOffice/Transactions/requests/update", { id, status })
            .then((res) => {
                if (res.status == 200) {
                    setSnackbar({
                        message: "Operation successful",
                        variant: "success",
                        show: true
                    });
                }
            }).catch((err) => {
                setSnackbar({
                    message: "An unexpected error occured, Please try again",
                    variant: "error",
                    show: true
                })
            }).finally(() => {
                getTransactions();
            });
    }

    const getTransactions = () => {
		if (_loading) {
			_refreshRequired = true;
			cancelRequest();
			return;
		}

		_loading = true;
		setLoading(true);

		Axios.get<FullTransaction[] | ITransactionRequest[]>(filter.path, { params: { start: dateRange.start, end: dateRange.end, searchQuery, filters: filter.filter },
			cancelToken: new Axios.CancelToken((c) => cancelRequest = c) })
			.then((res) => {
				if (res.status == 200) {

				    let data: FullTransaction[] | ITransactionRequest[] = res.data;

				    if (filter.path == "/api/backOffice/Transactions") {
                        data = res.data.sort((a:any, b:any) => {
                            var dateA = new Date(a.addedAt), dateB = new Date(b.addedAt);
                            return dateA > dateB ? -1 : dateA < dateB ? 1 : 0;
                        })
                    } else {
                        data = res.data.sort((a:any, b:any) => {
                            var dateA = new Date(a.created), dateB = new Date(b.created);
                            return dateA > dateB ? -1 : dateA < dateB ? 1 : 0;
                        })
                    }

                    console.log(data);

					setTransaction(data);
				}
			}).catch((err) => {
				console.log(err);
			}).finally(() => {
				_loading = false;

				if (_refreshRequired) {
					_refreshRequired = false;
					getTransactions();
				} else {
					setTimeout(() => setLoading(false), 1000);
				}
			})
	}


    return (
        <Wrapper pad={{bottom: "2rem"}} align="center">
            <SnackBarComponent
                message={snackbar.message}
                show={snackbar.show}
                variant={snackbar.variant}
                onClose={() => setSnackbar((s) => ({ ...s, show: false }))}
            />

            <Hero
				image={BackOfficeDashboardImage}
				text="Transactions"
			/>

            <AssertivePopup 
                title={popupState.title} open={popupState.open} 
                message={popupState.message} action={popupState.action} size={size}
                onClose={() => {
                    setPopupState({open: false, message: "", action: () => {}})
                }}
            />

            

            <Box justify="center" align="center">
                <Box
                    pad="large"
                    width={size !== "small" ? "90vw" : "80vw"}
                    background="white"
                    overflow={{ horizontal: "auto" }}
                    align="center"
                    justify="center"
                    round="small"
                    elevation="medium"
                    margin={{top: "large"}}
                >
                    <Box width="100%">
                        <Box width="100%">
                            <Form>
                                <Box width="100%" direction="row" margin={{vertical: "small"}} justify="between">
                                    <RangeCalendar onChange={(range: { start: Date, end: Date}, target: RangeCalendar) => {
                                        setDateRange({start: range.start, end: range.end});
                                    }} />

                                    <Box direction="row" gap="medium" pad={{top: "small"}} alignSelf="end">
                                        <Box direction="row" gap="2px" pad={{top: "small"}} margin={{bottom: "small"}} alignSelf="end">
                                            <Box width="32px" color="#9060EB" alignSelf="center">
                                                <i className="zwicon-filter" style={{color: "#9060EB"}} />
                                            </Box>

                                            <Select 
                                                value={filter.name}
                                                options={filters.map(f => f.name)}
                                                onChange={(option) => {
                                                    const fil = filters[option.selected];
                                                    setFilter(fil);
                                                    setIsRequest(fil.path == "/api/backOffice/Transactions/requests");
                                                }}
                                            />
                                        </Box>

                                        <Box direction="row" gap="2px" pad={{top: "small"}} alignSelf="end">
                                            <Box width="32px" color="#9060EB" alignSelf="center">
                                                <i className="zwicon-search" style={{color: "#9060EB"}} />
                                            </Box>

                                            <PatientInput waitInterval={3000} 
                                                placeholder="Search" value={searchQuery} onChange={(event) => {  setSearchQuery(event.target.value); }}/>
                                        </Box>
                                    </Box>
                                </Box>
                            </Form>
                        </Box>
                    </Box>

                    <Box width="100%" style={{position: "relative", minHeight: "500px"}} margin={{top: "30px"}}>
                        {!loading && (!transaction || transaction.length <= 0) ? (
                            <Box height="100%" width="100%" 
                                align="center" justify="center"
                                style={{
                                    position: "absolute",
                                    top: 0, left: 0,
                                }}
                            >
                                <Box height="300px" width="auto">
                                    <Image src={EmptyImage} fit="contain"/>
                                </Box>

                                <Box width="500px" align="center" justify="center">
                                    <Heading margin={{top: "0px"}} level="4">Huh? What happened to the Postman?</Heading>
                                    <Text weight={400} size="16px" textAlign="center">
                                        There doesn't seem to be any data to display, try searching or filtering to find
                                        what you want.
                                    </Text>
                                </Box>
                            </Box>
                        ) : null}
                        
                        <SlimSpinner active={loading}/>
                        
                        {transaction && !loading ? (
                            <>
                            {!isRequest ? (
                                <Table cellPadding="medium" className="hoverTable">
                                    <TableHeader
                                        style={{
                                            borderBottom: "solid 1px #ccc",
                                            fontSize: "14px !important",
                                        }}
                                    >
                                        <TableRow>
                                            <TableCell scope="col">
                                                Created
                                            </TableCell>
                                            <TableCell scope="col">
                                                Amount
                                            </TableCell>
                                            <TableCell scope="col">
                                                Payment Channel
                                            </TableCell>
                                            <TableCell scope="col">
                                                Type
                                            </TableCell>
                                            <TableCell scope="col">
                                                User
                                            </TableCell>
                                        </TableRow>
                                    </TableHeader>
    
                                    <TableBody>
                                        {(transaction as FullTransaction[]).map((t, index, array) => (
                                            <TableRow
                                                key={index}
                                                style={{
                                                    padding: ".1rem"
                                                }}
                                            >
                                                <TableCell>
                                                    {moment(t.addedAt).format("lll")}
                                                </TableCell>
    
                                                <TableCell>
                                                    {"₦ " + t.amount.toLocaleString()}
                                                </TableCell>
    
                                                <TableCell>
                                                    {t.paymentChannel.name}
                                                </TableCell>
    
                                                <TableCell>
                                                    {t.type}
                                                </TableCell>

                                                <TableCell>
                                                    {t.auxilaryUser.username}
                                                </TableCell>
                                            </TableRow>
                                        ))}
                                    </TableBody>
                                </Table>
                            ) : (
                                <Table cellPadding="medium" className="hoverTable">
                                    <TableHeader
                                        style={{
                                            borderBottom: "solid 1px #ccc",
                                            fontSize: "14px !important",
                                        }}
                                    >
                                        <TableRow>
                                            <TableCell scope="col">
                                                Created
                                            </TableCell>
                                            <TableCell scope="col">
                                                Transaction Date
                                            </TableCell>
                                            <TableCell scope="col">
                                                User
                                            </TableCell>

                                            <TableCell scope="col">
                                                Amount
                                            </TableCell>
                                            <TableCell scope="col">
                                                Payment Channel
                                            </TableCell>
                                            <TableCell scope="col">
                                                Paid To
                                            </TableCell>
                                            <TableCell scope="col">
                                                Paid From
                                            </TableCell>
    
                                            <TableCell scope="col">
                                                Description
                                            </TableCell>

                                            <TableCell scope="col">
                                                Status
                                            </TableCell>
                                        </TableRow>
                                    </TableHeader>
    
                                <TableBody>
                                    {(transaction as ITransactionRequest[]).map((t, index, array) => (
                                        <TableRow
                                            key={index}
                                            style={{
                                                padding: ".1rem"
                                            }}
                                        >
                                            <TableCell>
                                                {new Date(t.created as Date).toLocaleDateString()}
                                            </TableCell>
    
                                            <TableCell>
                                                {new Date(t.transactionDate as Date).toLocaleDateString()}
                                            </TableCell>

                                            <TableCell>
                                                {t.username}
                                            </TableCell>
    
                                            <TableCell>
                                                {"₦ " + t.amount.toLocaleString()}
                                            </TableCell>
    
                                            <TableCell>
                                                {t.paymentChannel.name}
                                            </TableCell>
    
                                            <TableCell>
                                                { t.platformBankAccount ? 
                                                    t.platformBankAccount.accountName + 
                                                    "              " + t.platformBankAccount.accountNumber +
                                                    "              " + BankList[t.platformBankAccount.bankId - 1].knownAs
                                                    : ""
                                                } 
                                            </TableCell>
    
                                            <TableCell>
                                                { t.userBankAccount ? 
                                                    t.userBankAccount.accountName + 
                                                    "              " + t.userBankAccount.accountNumber +
                                                    "              " + BankList[t.userBankAccount.bankId - 1].knownAs
                                                    : ""
                                                } 
                                            </TableCell>
    
                                            <TableCell>
                                                {t.description}
                                            </TableCell>

                                            <TableCell>
                                                {/* TODO: Use an enum */}
                                                <Box pad="0px"
                                                    onClick={() => {
                                                        //eventHandled = true;
                                                    }}
                                                >
                                                    <Select
                                                        value={t.status}
                                                        valueLabel={(
                                                            <Box
                                                                round="small"
                                                                overflow="hidden"
                                                                align="center"
                                                            >
                                                                {t.status}
                                                            </Box>
                                                        )}
                                                        icon={true}
                                                        disabled={t.status != "Pending"}
                                                        options={["Approved", "Declined", "Pending"]}
                                                        onChange={({ option }) => {
                                                            if (option == "Pending") { return; }  
                                                            setPopupState({
                                                                open: true,
                                                                title: `${option == "Approved" ? "Approve" : "Decline"} this transaction?`,
                                                                message: "Once a transaction is approved, " 
                                                                    + "the user's wallet will be credited with the claimed amount. This action cannot be reversed."
                                                                    , action: () => updateTransaction(t.id, option)})
                                                        }}
                                                        plain
                                                    />
                                                </Box>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                            )}
                            </>
                        ) : null}
                        
                        
                    </Box>
                </Box>
            </Box>
        </Wrapper>
    )
}

export default Transactions;
