import { DateRange } from "../../../Types";
import React, { Component } from "react";
import { Box, Select, Text, RangeInputProps } from "grommet";
import moment from "moment";
import CalendarDropDown from "./Calendar";

export interface RangeCalendarProps {
    onChange?: ((range: DateRange, target: RangeCalendar) => void);
}

type RangePointer = {
    name: string;
    range: () => DateRange;
}

const customRange: RangePointer = {
    name: "Custom", range: () => { return {} as DateRange; }
}

const ranges: RangePointer[] = [
    { name: "Today", range: () => { 
        return { start: moment().startOf("day").toDate(), 
            end: moment().endOf("day").toDate() }} 
    },
    { name: "Yesterday", range: () => { 
        const d = moment().subtract(1, "days");
        return { start: d.startOf("day").toDate(), 
            end: d.endOf("day").toDate() }} 
    },
    { name: "This week", range: () => {
        const d = moment();
        return { start: d.startOf("week").toDate(), 
            end: d.endOf("week").toDate() }}
    },
    { name: "Last week", range: () => {
        const d = moment().subtract(1, "weeks");
        return { start: d.startOf("week").toDate(), 
            end: d.endOf("week").toDate() }}
    },
    { name: "This month", range: () => {
        const d = moment();
        
        return { start: d.startOf("month").toDate(),
            end: d.endOf("month").toDate() }} 
    },
    { name: "Last Month", range: () => {
        const d = moment().subtract(1, "months");
        
        return { start: d.startOf("month").toDate(),
            end: d.endOf("month").toDate() }} 
    },
    { name: "Last 3 months", range: () => {
        const d = moment().subtract(3, "months");
        
        return { start: d.startOf("month").toDate(),
            end: moment().endOf("month").toDate() }} 
    }, 
    { name: "This year", range: () => {
        return { start: moment().startOf("year").toDate(),
        end: moment().endOf("year").toDate() }} 
    },
    { name: "Last year", range: () => {
        const d = moment().subtract(1, "years");
        
        return { start: d.startOf("year").toDate(),
            end: d.endOf("year").toDate() }} 
    },
    { name: "All time", range: () => {
        return { start: moment('2000', 'YYYY').startOf("year").toDate(),
            end: moment().endOf("year").toDate() }} 
    },
]

export default class RangeCalendar extends Component<RangeCalendarProps> {

    state = {
        range: ranges[4],
        dateRange: { start: ranges[4].range().start, end: ranges[4].range().end } as DateRange,
    }

    renderCount = 0;

    private onChange() {
        if (this.props.onChange) {
            this.props.onChange(this.state.dateRange, this);
        }
    }

    public render() {
        if (this.renderCount <= 0) {
            this.onChange();
        }

        this.renderCount++;
        return (
            <Box>
                <Box width="auto" alignSelf="start">
                    <Select 
                        value={this.state.range.name}
                        options={ranges.map((range) => range.name)}
                        onChange={(option) => {
                            let range = ranges[option.selected];

                            if (range.name == "Custom") {
                                return;
                            }

                            this.setState({range, dateRange: range.range()}, this.onChange);
                        }}
                    
                    />
                </Box>

                <Box direction="row" align="center" alignSelf="start" gap="small">
                    <Text>From</Text>
                    <CalendarDropDown date={this.state.dateRange.start}
                        onChange={(event: any) => {
                            this.setState({ 
                                dateRange: { start: moment(event.value).startOf("day").toDate(), 
                                end: this.state.dateRange.end }, 
                                range: customRange }, 
                                this.onChange);
                        }}
                    />
                    <Text>To</Text>
                    <CalendarDropDown date={this.state.dateRange.end}
                        onChange={(event: any) => {
                            this.setState({ 
                                dateRange: { start: this.state.dateRange.start, 
                                    end: moment(event.value).endOf("day").toDate() }, 
                                    range: customRange }, 
                                    this.onChange);
                        }}
                    />
                </Box>
            </Box>
        )
    }       
}