import React from "react";
import "./SlimSpinner.css";

const SlimSpinner = ({active} : {active: boolean}) => {
    return (
        active ? (
            <div className="spinner-box">
                <div className="circle-border">
                    <div className="circle-core"></div>
                </div>  
            </div>
        ) : null
    );
};

export default SlimSpinner;